import React from "react";
import { MDBContainer, MDBRow, MDBInput, MDBJumbotron } from "mdbreact";
import "../Style/Navbar.css";
import "../Style/card.css";
import "../Style/sexyButton.css";
import { BrowserView, MobileView } from "react-device-detect";

export default class ContactMe extends React.Component {
    constructor(props) {
        super(props);
        this.submitForm = this.submitForm.bind(this);
        this.state = {
            status: "",
        };
    }
    submitForm(ev) {
        ev.preventDefault();
        const form = ev.target;
        const data = new FormData(form);
        const xhr = new XMLHttpRequest();
        xhr.open(form.method, form.action);
        xhr.setRequestHeader("Accept", "application/json");
        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) return;
            if (xhr.status === 200) {
                form.reset();
                this.setState({ status: "SUCCESS" });
                this.ani();
                this.anitwo();
            } else {
                this.setState({ status: "ERROR" });
            }
        };
        xhr.send(data);
    }
    ani() {
        document.getElementById("plane").className = "animation";
        setTimeout(() => {
            document.getElementById("plane").remove();
        }, 500);
    }
    anitwo() {
        document.getElementById("bg").className = "animation2";
    }

    render() {
        const { status } = this.state;
        return (
            <MDBContainer className="mt-5 text-center">
                <div id="ContactMeSection" />
                <p className="biggerItalics">Contact Me!</p>
                <BrowserView>
                    <MDBJumbotron>
                        <MDBRow>
                            <div className="centreContact">
                                {/* <div className="text-alignLeft"> */}
                                <form
                                    onSubmit={this.submitForm}
                                    action="https://formspree.io/xoqkvole"
                                    method="POST"
                                >
                                    <div className="grey-text">
                                        <MDBInput
                                            label="Your name"
                                            icon="user"
                                            group
                                            type="text"
                                            validate
                                            error="wrong"
                                            success="right"
                                            name="name"
                                        />
                                        <MDBInput
                                            label="Your email"
                                            icon="envelope"
                                            group
                                            type="email"
                                            validate
                                            error="wrong"
                                            success="right"
                                            name="email"
                                        />
                                        <MDBInput
                                            label="Subject"
                                            icon="tag"
                                            group
                                            type="text"
                                            validate
                                            error="wrong"
                                            success="right"
                                            name="subject"
                                        />
                                        <MDBInput
                                            type="textarea"
                                            rows="2"
                                            label="Your message"
                                            icon="pencil-alt"
                                            name="message"
                                        />
                                        <div className="text-center">
                                            <div className="boxToHidePlane">
                                                <div class="around around-boarder">
                                                    <button
                                                        className="sexyButton sexyButton-boarder"
                                                        onClick={() => {
                                                            if (
                                                                status ===
                                                                "SUCCESS"
                                                            ) {
                                                            } else if (
                                                                status ===
                                                                "ERROR"
                                                            ) {
                                                            }
                                                        }}
                                                    >
                                                        <img
                                                            src="https://i.cloudup.com/gBzAn-oW_S-2000x2000.png"
                                                            width="64px"
                                                            height="64px"
                                                            id="plane"
                                                            alt="small paper plane"
                                                        />
                                                        <img
                                                            src="https://i.cloudup.com/2ZAX3hVsBE-3000x3000.png"
                                                            id="bg"
                                                            width="32px"
                                                            height="32px"
                                                            alt="small tick"
                                                            style={{
                                                                opacity: 0,
                                                            }}
                                                        />
                                                    </button>
                                                </div>
                                            </div>
                                        </div>

                                        {status === "SUCCESS" && (
                                            <p>
                                                Thanks! I'll reply as soon as I
                                                can!
                                            </p>
                                        )}

                                        {status === "ERROR" && (
                                            <p>
                                                Something went wrong... Try
                                                again!
                                            </p>
                                        )}
                                    </div>
                                </form>
                            </div>
                        </MDBRow>
                    </MDBJumbotron>
                </BrowserView>
                <MobileView>
                    <MDBJumbotron>
                        <MDBRow>
                            <div className="centreContactMobile">
                                {/* <div className="text-alignLeft"> */}
                                <form
                                    onSubmit={this.submitForm}
                                    action="https://formspree.io/xoqkvole"
                                    method="POST"
                                >
                                    <div className="grey-text">
                                        <MDBInput
                                            label="Your name"
                                            icon="user"
                                            group
                                            type="text"
                                            validate
                                            error="wrong"
                                            success="right"
                                            name="name"
                                        />
                                        <MDBInput
                                            label="Your email"
                                            icon="envelope"
                                            group
                                            type="email"
                                            validate
                                            error="wrong"
                                            success="right"
                                            name="email"
                                        />
                                        <MDBInput
                                            label="Subject"
                                            icon="tag"
                                            group
                                            type="text"
                                            validate
                                            error="wrong"
                                            success="right"
                                            name="subject"
                                        />
                                        <MDBInput
                                            type="textarea"
                                            rows="2"
                                            label="Your message"
                                            icon="pencil-alt"
                                            name="message"
                                        />
                                        <div className="text-center">
                                            <div className="boxToHidePlane">
                                                <div class="around around-boarder">
                                                    <button
                                                        className="sexyButton sexyButton-boarder"
                                                        onClick={() => {
                                                            if (
                                                                status ===
                                                                "SUCCESS"
                                                            ) {
                                                            } else if (
                                                                status ===
                                                                "ERROR"
                                                            ) {
                                                            }
                                                        }}
                                                    >
                                                        <img
                                                            src="https://i.cloudup.com/gBzAn-oW_S-2000x2000.png"
                                                            width="64px"
                                                            height="64px"
                                                            id="plane"
                                                            alt="small paper plane"
                                                        />
                                                        <img
                                                            src="https://i.cloudup.com/2ZAX3hVsBE-3000x3000.png"
                                                            id="bg"
                                                            width="32px"
                                                            height="32px"
                                                            alt="small tick"
                                                            style={{
                                                                opacity: 0,
                                                            }}
                                                        />
                                                    </button>
                                                </div>
                                            </div>
                                        </div>

                                        {status === "SUCCESS" && (
                                            <p>
                                                Thanks! I'll reply as soon as I
                                                can!
                                            </p>
                                        )}

                                        {status === "ERROR" && (
                                            <p>
                                                Something went wrong... Try
                                                again!
                                            </p>
                                        )}
                                    </div>
                                </form>
                            </div>
                        </MDBRow>
                    </MDBJumbotron>
                </MobileView>
            </MDBContainer>
        );
    }
}
