import React from "react";
import "../Style/name.css";
import "../Style/socialMedia.css";
import { MDBContainer, MDBFooter, MDBIcon, MDBBtn } from "mdbreact";

function openNewPage(url) {
    window.open(url, "_blank");
}
export default class FooterPage extends React.Component {
    render() {
        return (
            <div>
                <MDBFooter color="blue" className="font-small pt-4 mt-4">
                    <MDBContainer fluid className="text-center text-md-left">
                        <p className="bigItalicsCentre">
                            Thank You For Visiting My Webiste!
                        </p>
                        <p className="smallerItalicsCentre">
                            Please also visit:
                        </p>
                        <div className="centered">
                            <MDBBtn
                                onClick={() => {
                                    openNewPage(
                                        "https://www.linkedin.com/in/tomasz-wisniowski-495312181/"
                                    );
                                }}
                                gradient="blue"
                                size="sm"
                                style={{ borderRadius: "10px" }}
                            >
                                <MDBIcon fab icon="linkedin" size="4x" />
                            </MDBBtn>
                            <MDBBtn
                                onClick={() => {
                                    openNewPage("https://github.com/Wisniowt");
                                }}
                                gradient="blue"
                                size="sm"
                                style={{ borderRadius: "10px" }}
                            >
                                <MDBIcon fab icon="github-square" size="4x" />
                            </MDBBtn>
                        </div>
                    </MDBContainer>
                    <div className="footer-copyright text-center py-3">
                        <MDBContainer fluid>
                            {new Date().getFullYear()} Copyright:
                            <p> Wisniowt.github.io/my-website-resume/ </p>
                        </MDBContainer>
                    </div>
                </MDBFooter>
            </div>
        );
    }
}
