import React from "react";
import "../Style/Navbar.css";
import {
    MDBJumbotron,
    MDBContainer,
    MDBRow,
    MDBCol,
    MDBCardTitle,
    MDBCardImage,
    MDBCardText,
} from "mdbreact";
import IBMResized from "../Assets/IBMResized.jpg";

export default class Experiance extends React.Component {
    render() {
        return (
            <div id="ExperianceSection">
                <MDBContainer className="mt-5 text-center">
                    <p className="biggerItalics">Experience</p>

                    <MDBJumbotron className="text-center">
                        <MDBCardTitle className="card-title h4 pb-2">
                            <p className="bigBlackItalics">IBM Ireland</p>
                        </MDBCardTitle>
                        <MDBRow>
                            <MDBCol md="6">
                                <MDBCardImage
                                    src={IBMResized}
                                    className="roundImageShadow"
                                />
                            </MDBCol>

                            <MDBCol md="6">
                                <MDBCardText>
                                    <div className="indentedBoxExperience">
                                        <ul>
                                            <li>
                                                In July 2019, I was offered a
                                                job as an intern in IBM Ireland.
                                            </li>
                                            <br />
                                            <li>
                                                I joined a team of data
                                                scientists and software
                                                developers called
                                                <i>"IBM Innovation Exchange"</i>
                                                .
                                            </li>
                                            <br />
                                            <li>
                                                While on the team I helped
                                                develop user interfaces for a
                                                number of European projects,
                                                funded by the Horizon 2020
                                                Research and Innovation
                                                programme. I also provided
                                                suggestions for additional
                                                features and improvements.
                                            </li>
                                            <br />
                                            <li>
                                                There I learned technologies
                                                such as React and deepened my
                                                understanding of machine
                                                learning.
                                            </li>
                                            <br />
                                            <li>
                                                This internship gave me
                                                experience working on a
                                                professional team and also
                                                thought me the Agile
                                                methodology.
                                            </li>
                                        </ul>
                                    </div>
                                </MDBCardText>

                                <MDBCol
                                    className="d-flex justify-content-center mt-4"
                                    md="12"
                                >
                                    <MDBCol
                                        md="3"
                                        className="d-flex justify-content-around"
                                    ></MDBCol>
                                </MDBCol>
                            </MDBCol>
                        </MDBRow>
                    </MDBJumbotron>
                </MDBContainer>
            </div>
        );
    }
}
