import React from "react";
import "../Style/Navbar.css";
import "../Style/name.css";
import {
    MDBNavbar,
    MDBNavbarBrand,
    MDBNavbarNav,
    MDBNavbarToggler,
    MDBCollapse,
    MDBNavItem,
    MDBNavLink,
    MDBContainer,
    MDBMask,
    MDBView,
    MDBAnimation,
} from "mdbreact";
import { BrowserRouter as Router } from "react-router-dom";
// import PDFModal from "../Components/PDFModal";
import { Link } from "react-scroll";
import FinalYearProject from "../Assets/FinalYearProject.pdf";

export default class MyNavbar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            collapse: false,
            isWideEnough: false,
        };
        this.onClick = this.onClick.bind(this);
    }

    onClick() {
        this.setState({
            collapse: !this.state.collapse,
        });
    }

    render() {
        return (
            <div>
                <header>
                    <Router>
                        <MDBNavbar
                            color="bg-primary"
                            fixed="top"
                            dark
                            expand="md"
                            scrolling
                            transparent
                        >
                            <MDBNavbarBrand href="/">
                                <Link
                                    activeClass="active"
                                    to="TopSection"
                                    spy={true}
                                    smooth={true}
                                    offset={-70}
                                    duration={500}
                                >
                                    <strong>Tomasz Wisniowski</strong>
                                </Link>
                            </MDBNavbarBrand>
                            {!this.state.isWideEnough && (
                                <MDBNavbarToggler onClick={this.onClick} />
                            )}
                            <MDBCollapse isOpen={this.state.collapse} navbar>
                                <MDBNavbarNav left>
                                    <MDBNavItem active></MDBNavItem>
                                    <MDBNavItem>
                                        <MDBNavLink to="#">
                                            <Link
                                                activeClass="active"
                                                to="EducationSection"
                                                spy={true}
                                                smooth={true}
                                                offset={-70}
                                                duration={500}
                                            >
                                                Education
                                            </Link>
                                        </MDBNavLink>
                                    </MDBNavItem>
                                    <MDBNavItem>
                                        <MDBNavLink to="#">
                                            <Link
                                                activeClass="active"
                                                to="ExperianceSection"
                                                spy={true}
                                                smooth={true}
                                                offset={-70}
                                                duration={500}
                                            >
                                                Experience
                                            </Link>
                                        </MDBNavLink>
                                    </MDBNavItem>
                                    <MDBNavItem>
                                        <MDBNavLink to="#">
                                            <Link
                                                activeClass="active"
                                                to="ProjectsSection"
                                                spy={true}
                                                smooth={true}
                                                offset={-70}
                                                duration={500}
                                            >
                                                Projects
                                            </Link>
                                        </MDBNavLink>
                                    </MDBNavItem>
                                    <MDBNavItem>
                                        <MDBNavLink to="#">
                                            <Link
                                                activeClass="active"
                                                to="SkillsSection"
                                                spy={true}
                                                smooth={true}
                                                offset={-70}
                                                duration={500}
                                            >
                                                Skills
                                            </Link>
                                        </MDBNavLink>
                                    </MDBNavItem>
                                    <MDBNavItem>
                                        <MDBNavLink to="#">
                                            <Link
                                                activeClass="active"
                                                to="InterestsSection"
                                                spy={true}
                                                smooth={true}
                                                offset={-70}
                                                duration={500}
                                            >
                                                Interests
                                            </Link>
                                        </MDBNavLink>
                                    </MDBNavItem>
                                    <MDBNavItem>
                                        <MDBNavLink to="#">
                                            <Link
                                                activeClass="active"
                                                to="ContactMeSection"
                                                spy={true}
                                                smooth={true}
                                                offset={-70}
                                                duration={500}
                                            >
                                                Contact Me
                                            </Link>
                                        </MDBNavLink>
                                    </MDBNavItem>
                                    <MDBNavItem>
                                        <MDBNavLink
                                            to={FinalYearProject}
                                            target="_blank"
                                        >
                                            {/* <PDFModal /> */}
                                            Resume
                                        </MDBNavLink>
                                    </MDBNavItem>
                                </MDBNavbarNav>
                            </MDBCollapse>
                        </MDBNavbar>
                    </Router>
                    <div id="TopSection"></div>
                    <MDBView src="https://mdbootstrap.com/img/Photos/Others/img%20(40).jpg">
                        <MDBMask
                            overlay="purple-light"
                            className="flex-center flex-column text-white text-center"
                        >
                            <MDBContainer>
                                <div className="text-center">
                                    <MDBAnimation type="fadeIn" duration="3s">
                                        <p className="bigItalics"> Hey, I'm</p>
                                    </MDBAnimation>

                                    <MDBAnimation type="fadeIn" duration="5s">
                                        <p className="biggestItalics">
                                            Tomasz Wisniowski
                                        </p>
                                        <hr className="blackHardLine" />
                                    </MDBAnimation>
                                    <MDBAnimation type="fadeIn" duration="5s">
                                        <p></p>
                                    </MDBAnimation>
                                </div>
                            </MDBContainer>
                        </MDBMask>
                    </MDBView>
                </header>
            </div>
        );
    }
}
