import React from "react";
import "../Style/Navbar.css";
import "../Style/card.css";
import "../Style/name.css";
import RugbyCropped from "../Assets/RugbyCropped.jpg";
import GymCropped from "../Assets/GymCropped.jpg";
import ClimbingCropped from "../Assets/ClimbingCropped.jpg";
import GamingCropped from "../Assets/GamingCropped.jpg";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import Tooltip from "react-bootstrap/Tooltip";
import Button from "react-bootstrap/Button";
import { BrowserView, MobileView } from "react-device-detect";

import { MDBJumbotron, MDBContainer, MDBRow, MDBCardImage } from "mdbreact";

export default class Interests extends React.Component {
    render() {
        function renderTooltip(number) {
            if (number === 1) {
                return (
                    <Tooltip id="button-tooltip">
                        <ul>
                            <li>
                                I played rugby for Clongowes for 5 years and 1
                                year for Trinity.
                            </li>
                            <li>
                                I had to stop playing due to injuries but can't
                                wait to get back on the field!
                            </li>
                        </ul>
                    </Tooltip>
                );
            }
            if (number === 2) {
                return (
                    <Tooltip id="button-tooltip">
                        <ul>
                            <li>I try to go to the 5 days every week.</li>
                            <li>
                                In second year of college I became very immersed
                                in the topic of gym, fitness and bodybuilding.
                            </li>
                            <li>
                                I even developed some of my own software to
                                calculate my caloric intake, energy expenditure
                                and other metrics.
                            </li>
                            <li>
                                Becoming a fully qualified personal trainer is
                                one of my dreams, which I will definitely pursue
                                in the near future.
                            </li>
                        </ul>
                    </Tooltip>
                );
            }
            if (number === 3) {
                return (
                    <Tooltip id="button-tooltip">
                        <ul>
                            <li>In second year I also began rock climbing.</li>
                            <li>
                                I quickly obtained my climbing license which
                                allows me to help and teach new climbers.
                            </li>
                            <li>
                                Outdoor climbing is something I have yet to try.
                            </li>
                        </ul>
                    </Tooltip>
                );
            }
            if (number === 4) {
                return (
                    <Tooltip id="button-tooltip">
                        <ul>
                            <li>
                                I like to develop my own software to automate
                                some aspects of my life. This also helps me
                                learn new technologies and skills, making me a
                                more rounded professional.
                            </li>
                            <li>
                                I also frequently review my own code, find ways
                                to optimise and build new websites for myself or
                                my friends.
                            </li>
                            <li>
                                Just like most computer scientists, I also enjoy
                                gaming.
                            </li>
                        </ul>
                    </Tooltip>
                );
            }
        }
        return (
            <div id="InterestsSection">
                <MobileView>
                    <MDBContainer className="mt-5 text-center" fluid>
                        <p className="biggerItalics">Interests</p>
                        <MDBJumbotron className="text-center">
                            <MDBRow>
                                <div className="centreImages">
                                    <OverlayTrigger
                                        placement="bottom"
                                        delay={{ show: 250, hide: 250 }}
                                        overlay={renderTooltip(1)}
                                    >
                                        <Button variant="flat">
                                            <div className="firstClass">
                                                <div className="roundImageShadowInterests">
                                                    <MDBCardImage
                                                        src={RugbyCropped}
                                                        className="roundedCornersBorder"
                                                    />
                                                </div>
                                            </div>
                                        </Button>
                                    </OverlayTrigger>
                                    <OverlayTrigger
                                        placement="bottom"
                                        delay={{ show: 250, hide: 250 }}
                                        overlay={renderTooltip(2)}
                                    >
                                        <Button variant="flat">
                                            <div className="firstClass">
                                                <div className="roundImageShadowInterests">
                                                    <MDBCardImage
                                                        src={GymCropped}
                                                        className="roundedCornersBorder"
                                                    />
                                                </div>
                                            </div>
                                        </Button>
                                    </OverlayTrigger>
                                    <OverlayTrigger
                                        placement="bottom"
                                        delay={{ show: 250, hide: 250 }}
                                        overlay={renderTooltip(3)}
                                    >
                                        <Button variant="flat">
                                            <div className="firstClass">
                                                <div className="roundImageShadowInterests">
                                                    <MDBCardImage
                                                        src={ClimbingCropped}
                                                        className="roundedCornersBorder"
                                                    />
                                                </div>
                                            </div>
                                        </Button>
                                    </OverlayTrigger>
                                    <OverlayTrigger
                                        placement="bottom"
                                        delay={{ show: 250, hide: 400 }}
                                        overlay={renderTooltip(4)}
                                    >
                                        <Button variant="flat">
                                            <div className="firstClass">
                                                <div className="roundImageShadowInterests">
                                                    <MDBCardImage
                                                        src={GamingCropped}
                                                        className="roundedCornersBorder"
                                                    />
                                                </div>
                                            </div>
                                        </Button>
                                    </OverlayTrigger>
                                </div>
                            </MDBRow>
                        </MDBJumbotron>
                    </MDBContainer>
                </MobileView>
                <BrowserView>
                    <MDBContainer className="mt-5 text-center" fluid>
                        <p className="biggerItalics">Interests</p>
                        <MDBJumbotron className="text-center">
                            <div className="centreImages">
                                <MDBRow>
                                    <OverlayTrigger
                                        placement="bottom"
                                        delay={{ show: 250, hide: 250 }}
                                        overlay={renderTooltip(1)}
                                    >
                                        <Button variant="flat">
                                            <div className="firstClass">
                                                <div className="roundImageShadowInterests">
                                                    <MDBCardImage
                                                        src={RugbyCropped}
                                                        className="roundedCornersBorder"
                                                    />
                                                </div>
                                            </div>
                                        </Button>
                                    </OverlayTrigger>
                                    <OverlayTrigger
                                        placement="bottom"
                                        delay={{ show: 250, hide: 250 }}
                                        overlay={renderTooltip(2)}
                                    >
                                        <Button variant="flat">
                                            <div className="firstClass">
                                                <div className="roundImageShadowInterests">
                                                    <MDBCardImage
                                                        src={GymCropped}
                                                        className="roundedCornersBorder"
                                                    />
                                                </div>
                                            </div>
                                        </Button>
                                    </OverlayTrigger>
                                    <OverlayTrigger
                                        placement="bottom"
                                        delay={{ show: 250, hide: 250 }}
                                        overlay={renderTooltip(3)}
                                    >
                                        <Button variant="flat">
                                            <div className="firstClass">
                                                <div className="roundImageShadowInterests">
                                                    <MDBCardImage
                                                        src={ClimbingCropped}
                                                        className="roundedCornersBorder"
                                                    />
                                                </div>
                                            </div>
                                        </Button>
                                    </OverlayTrigger>
                                    <OverlayTrigger
                                        placement="bottom"
                                        delay={{ show: 250, hide: 400 }}
                                        overlay={renderTooltip(4)}
                                    >
                                        <Button variant="flat">
                                            <div className="firstClass">
                                                <div className="roundImageShadowInterests">
                                                    <MDBCardImage
                                                        src={GamingCropped}
                                                        className="roundedCornersBorder"
                                                    />
                                                </div>
                                            </div>
                                        </Button>
                                    </OverlayTrigger>
                                </MDBRow>
                            </div>
                        </MDBJumbotron>
                    </MDBContainer>
                </BrowserView>
            </div>
        );
    }
}
